module.exports = "// tslint:disable:no-any\r\nimport { Component } from '@angular/core';\r\n\r\nconst options = [{\r\n  value: 'zhejiang',\r\n  label: 'Zhejiang',\r\n  children: [{\r\n    value: 'hangzhou',\r\n    label: 'Hangzhou',\r\n    children: [{\r\n      value: 'xihu',\r\n      label: 'West Lake',\r\n      isLeaf: true\r\n    }]\r\n  }, {\r\n    value: 'ningbo',\r\n    label: 'Ningbo',\r\n    children: [{\r\n      value: 'dongqianlake',\r\n      label: 'Dongqian Lake',\r\n      isLeaf: true\r\n    }]\r\n  }]\r\n}, {\r\n  value: 'jiangsu',\r\n  label: 'Jiangsu',\r\n  children: [{\r\n    value: 'nanjing',\r\n    label: 'Nanjing',\r\n    children: [{\r\n      value: 'zhonghuamen',\r\n      label: 'Zhong Hua Men',\r\n      isLeaf: true\r\n    }]\r\n  }]\r\n}];\r\n\r\n@Component({\r\n  selector: 'nz-demo-cascader-change-on-function',\r\n  template: `\r\n    <nz-cascader\r\n      [nzChangeOn]=\"validate\"\r\n      [nzOptions]=\"nzOptions\"\r\n      [(ngModel)]=\"values\"\r\n      (ngModelChange)=\"onChanges($event)\">\r\n    </nz-cascader>`,\r\n  styles  : [\r\n    `\r\n    .ant-cascader-picker {\r\n      width: 300px;\r\n    }\r\n    `\r\n  ]\r\n})\r\nexport class NzDemoCascaderChangeOnFunctionComponent {\r\n  /** init data */\r\n  nzOptions = options;\r\n\r\n  /** ngModel value */\r\n  public values: any[] = null;\r\n\r\n  public onChanges(values: any): void {\r\n    console.log(values, this.values);\r\n  }\r\n\r\n  public validate(option: any, index: number): boolean {\r\n    const value = option.value;\r\n    return ['hangzhou', 'xihu', 'nanjing', 'zhonghuamen'].indexOf(value) >= 0;\r\n  }\r\n}\r\n"