module.exports = "// tslint:disable:no-any\r\nimport { Component } from '@angular/core';\r\n\r\nconst provinces = [{\r\n  value: 'zhejiang',\r\n  label: 'Zhejiang'\r\n}, {\r\n  value: 'jiangsu',\r\n  label: 'Jiangsu'\r\n}];\r\n\r\nconst cities = {\r\n  zhejiang: [{\r\n    value: 'hangzhou',\r\n    label: 'Hangzhou'\r\n  }, {\r\n    value: 'ningbo',\r\n    label: 'Ningbo',\r\n    isLeaf: true\r\n  }],\r\n  jiangsu: [{\r\n    value: 'nanjing',\r\n    label: 'Nanjing'\r\n  }]\r\n};\r\n\r\nconst scenicspots = {\r\n  hangzhou: [{\r\n    value: 'xihu',\r\n    label: 'West Lake',\r\n    isLeaf: true\r\n  }],\r\n  nanjing: [{\r\n    value: 'zhonghuamen',\r\n    label: 'Zhong Hua Men',\r\n    isLeaf: true\r\n  }]\r\n};\r\n\r\n@Component({\r\n  selector: 'nz-demo-cascader-lazy',\r\n  template: `\r\n    <nz-cascader\r\n      [(ngModel)]=\"values\"\r\n      [nzLoadData]=\"loadData\"\r\n      (ngModelChange)=\"onChanges($event)\">\r\n    </nz-cascader>\r\n  `,\r\n  styles  : [\r\n    `\r\n    .ant-cascader-picker {\r\n      width: 300px;\r\n    }\r\n    `\r\n  ]\r\n})\r\nexport class NzDemoCascaderLazyComponent {\r\n\r\n  /** ngModel value */\r\n  public values: any[] = null;\r\n\r\n  public onChanges(values: any): void {\r\n    console.log(values);\r\n  }\r\n\r\n  /** load data async execute by `nzLoadData` method */\r\n  public loadData(node: any, index: number): PromiseLike<any> {\r\n    return new Promise((resolve) => {\r\n      setTimeout(() => {\r\n        if (index < 0) { // if index less than 0 it is root node\r\n          node.children = provinces;\r\n        } else if (index === 0) {\r\n          node.children = cities[node.value];\r\n        } else {\r\n          node.children = scenicspots[node.value];\r\n        }\r\n        resolve();\r\n      }, 1000);\r\n    });\r\n  }\r\n\r\n}\r\n"