module.exports = "// tslint:disable:no-any\r\nimport { Component } from '@angular/core';\r\n\r\nconst options = [{\r\n  value: 'zhejiang',\r\n  label: 'Zhejiang',\r\n  children: [{\r\n    value: 'hangzhou',\r\n    label: 'Hangzhou',\r\n    children: [{\r\n      value: 'xihu',\r\n      label: 'West Lake',\r\n      isLeaf: true\r\n    }]\r\n  }, {\r\n    value: 'ningbo',\r\n    label: 'Ningbo',\r\n    isLeaf: true\r\n  }]\r\n}, {\r\n  value: 'jiangsu',\r\n  label: 'Jiangsu',\r\n  children: [{\r\n    value: 'nanjing',\r\n    label: 'Nanjing',\r\n    children: [{\r\n      value: 'zhonghuamen',\r\n      label: 'Zhong Hua Men',\r\n      isLeaf: true\r\n    }]\r\n  }]\r\n}];\r\n\r\n@Component({\r\n  selector: 'nz-demo-cascader-modal',\r\n  template: `\r\n    <nz-modal [(nzVisible)]=\"isVisible\" nzTitle=\"Please select\" (nzOnCancel)=\"handleCancel($event)\" (nzOnOk)=\"handleOk($event)\">\r\n      <nz-cascader\r\n        [nzOptions]=\"nzOptions\"\r\n        [(ngModel)]=\"values\"\r\n        (ngModelChange)=\"onChanges($event)\">\r\n      </nz-cascader>\r\n    </nz-modal>\r\n\r\n    <button nz-button (click)=\"open()\">Open Dialog</button>\r\n    `,\r\n  styles  : [\r\n    `\r\n    .ant-cascader-picker {\r\n      width: 300px;\r\n    }\r\n    `\r\n  ]\r\n})\r\nexport class NzDemoCascaderModalComponent {\r\n  /** init data */\r\n  public nzOptions = options;\r\n\r\n  /** ngModel value */\r\n  public values: any[] = null;\r\n\r\n  public isVisible = false;\r\n\r\n  public onChanges(values: any): void {\r\n    console.log(values, this.values);\r\n  }\r\n\r\n  public open(): void {\r\n    this.isVisible = true;\r\n  }\r\n\r\n  handleOk($event: MouseEvent): void {\r\n    console.log('Button ok clicked!', this.values);\r\n    this.isVisible = false;\r\n  }\r\n\r\n  handleCancel($event: MouseEvent): void {\r\n    console.log('Button cancel clicked!', $event);\r\n    this.isVisible = false;\r\n  }\r\n\r\n}\r\n"