module.exports = "// tslint:disable:no-any\r\nimport { Component } from '@angular/core';\r\nimport { FormBuilder, FormGroup, Validators } from '@angular/forms';\r\n\r\nconst options = [ {\r\n  value   : 'zhejiang',\r\n  label   : 'Zhejiang',\r\n  children: [ {\r\n    value   : 'hangzhou',\r\n    label   : 'Hangzhou',\r\n    children: [ {\r\n      value : 'xihu',\r\n      label : 'West Lake',\r\n      isLeaf: true\r\n    } ]\r\n  }, {\r\n    value : 'ningbo',\r\n    label : 'Ningbo',\r\n    isLeaf: true\r\n  } ]\r\n}, {\r\n  value   : 'jiangsu',\r\n  label   : 'Jiangsu',\r\n  children: [ {\r\n    value   : 'nanjing',\r\n    label   : 'Nanjing',\r\n    children: [ {\r\n      value : 'zhonghuamen',\r\n      label : 'Zhong Hua Men',\r\n      isLeaf: true\r\n    } ]\r\n  } ]\r\n} ];\r\n\r\n@Component({\r\n  selector: 'nz-demo-cascader-reactive-form',\r\n  template: `\r\n    <form [formGroup]=\"form\" novalidate>\r\n      <nz-cascader\r\n        [nzOptions]=\"nzOptions\"\r\n        (nzChange)=\"onChanges($event)\"\r\n        [formControlName]=\"'name'\">\r\n      </nz-cascader>\r\n    </form>\r\n    <br>\r\n    <button nz-button (click)=\"reset()\">Reset</button>\r\n    <button nz-button (click)=\"submit()\">Submit</button>\r\n  `,\r\n  styles  : [\r\n      `\r\n      .ant-cascader-picker {\r\n        width: 300px;\r\n      }\r\n\r\n      button {\r\n        margin-right: 8px;\r\n      }\r\n    `\r\n  ]\r\n})\r\nexport class NzDemoCascaderReactiveFormComponent {\r\n  /** init data */\r\n  nzOptions = options;\r\n\r\n  public form: FormGroup;\r\n\r\n  constructor(private fb: FormBuilder) {\r\n    this.createForm();\r\n  }\r\n\r\n  private createForm(): void {\r\n    this.form = this.fb.group({\r\n      name: [ null, Validators.required ]\r\n    });\r\n  }\r\n\r\n  public reset(): void {\r\n    this.form.reset();\r\n    console.log(this.form.value);\r\n  }\r\n\r\n  public submit(): void {\r\n    console.log(this.form.value);\r\n  }\r\n\r\n  public onChanges(values: any): void {\r\n    console.log(values);\r\n  }\r\n}\r\n"