module.exports = "// tslint:disable:no-any\r\nimport { Component, OnInit } from '@angular/core';\r\n\r\nconst options = [{\r\n  value: 'zhejiang',\r\n  label: 'Zhejiang',\r\n  children: [{\r\n    value: 'hangzhou',\r\n    label: 'Hangzhou',\r\n    children: [{\r\n      value: 'xihu',\r\n      label: 'West Lake',\r\n      isLeaf: true\r\n    }]\r\n  }, {\r\n    value: 'ningbo',\r\n    label: 'Ningbo',\r\n    isLeaf: true\r\n  }]\r\n}, {\r\n  value: 'jiangsu',\r\n  label: 'Jiangsu',\r\n  children: [{\r\n    value: 'nanjing',\r\n    label: 'Nanjing',\r\n    children: [{\r\n      value: 'zhonghuamen',\r\n      label: 'Zhong Hua Men',\r\n      isLeaf: true\r\n    }]\r\n  }]\r\n}];\r\n\r\n@Component({\r\n  selector: 'nz-demo-cascader-default-value-and-asyn-options',\r\n  template: `\r\n    <nz-cascader\r\n      [(ngModel)]=\"values\"\r\n      [nzOptions]=\"nzOptions\"\r\n      (ngModelChange)=\"onChanges($event)\">\r\n    </nz-cascader>\r\n  `,\r\n  styles  : [\r\n    `\r\n    .ant-cascader-picker {\r\n      width: 300px;\r\n    }\r\n    `\r\n  ]\r\n})\r\nexport class NzDemoCascaderDefaultValueAndAsynOptionsComponent implements OnInit {\r\n  /** init data */\r\n  public nzOptions = null;\r\n\r\n  /** ngModel value */\r\n  public values: any[] = ['zhejiang', 'hangzhou', 'xihu'];\r\n\r\n  public onChanges(values: any): void {\r\n    console.log(values, this.values);\r\n  }\r\n\r\n  ngOnInit(): void {\r\n    setTimeout(() => {\r\n      this.nzOptions = options;\r\n    }, 500);\r\n  }\r\n\r\n}\r\n"