module.exports = "// tslint:disable:no-any\r\nimport { Component, OnInit } from '@angular/core';\r\n\r\nconst options = [{\r\n  value: 'zhejiang',\r\n  label: 'Zhejiang',\r\n  children: [{\r\n    value: 'hangzhou',\r\n    label: 'Hangzhou',\r\n    children: [{\r\n      value: 'xihu',\r\n      label: 'West Lake',\r\n      isLeaf: true\r\n    }]\r\n  }, {\r\n    value: 'ningbo',\r\n    label: 'Ningbo',\r\n    isLeaf: true\r\n  }]\r\n}, {\r\n  value: 'jiangsu',\r\n  label: 'Jiangsu',\r\n  children: [{\r\n    value: 'nanjing',\r\n    label: 'Nanjing',\r\n    children: [{\r\n      value: 'zhonghuamen',\r\n      label: 'Zhong Hua Men',\r\n      isLeaf: true\r\n    }]\r\n  }]\r\n}];\r\n\r\nconst otherOptions = [{\r\n  value: 'fujian',\r\n  label: 'Fujian',\r\n  children: [{\r\n    value: 'xiamen',\r\n    label: 'Xiamen',\r\n    children: [{\r\n      value: 'Kulangsu',\r\n      label: 'Kulangsu',\r\n      isLeaf: true\r\n    }]\r\n  }]\r\n}, {\r\n  value: 'guangxi',\r\n  label: 'Guangxi',\r\n  children: [{\r\n    value: 'guilin',\r\n    label: 'Guilin',\r\n    children: [{\r\n      value: 'Lijiang',\r\n      label: 'Li Jiang River',\r\n      isLeaf: true\r\n    }]\r\n  }]\r\n}];\r\n\r\n@Component({\r\n  selector: 'nz-demo-cascader-basic',\r\n  template: `\r\n    <nz-cascader\r\n      [nzOptions]=\"nzOptions\"\r\n      [(ngModel)]=\"values\"\r\n      (ngModelChange)=\"onChanges($event)\">\r\n    </nz-cascader>\r\n    <a href=\"javascript:;\" (click)=\"changeNzOptions()\" class=\"change-options\">\r\n      Change Options\r\n    </a>\r\n    `,\r\n  styles  : [\r\n    `\r\n    .ant-cascader-picker {\r\n      width: 300px;\r\n    }\r\n    .change-options {\r\n      display: inline-block;\r\n      font-size: 12px;\r\n      margin-top: 8px;\r\n    }\r\n    `\r\n  ]\r\n})\r\nexport class NzDemoCascaderBasicComponent implements OnInit {\r\n  /** init data */\r\n  public nzOptions = null;\r\n\r\n  /** ngModel value */\r\n  public values: any[] = null;\r\n\r\n  ngOnInit(): void {\r\n    // let's set nzOptions in a asynchronous way\r\n    setTimeout(() => {\r\n      this.nzOptions = options;\r\n    }, 100);\r\n  }\r\n\r\n  public changeNzOptions(): void {\r\n    if (this.nzOptions === options) {\r\n      this.nzOptions = otherOptions;\r\n    } else {\r\n      this.nzOptions = options;\r\n    }\r\n  }\r\n\r\n  public onChanges(values: any): void {\r\n    console.log(values, this.values);\r\n  }\r\n}\r\n"