module.exports = "// tslint:disable:no-any\r\nimport { Component } from '@angular/core';\r\n\r\nconst options = [{\r\n  value: 'zhejiang',\r\n  label: 'Zhejiang',\r\n  children: [{\r\n    value: 'hangzhou',\r\n    label: 'Hangzhou',\r\n    children: [{\r\n      value: 'xihu',\r\n      label: 'West Lake',\r\n      isLeaf: true\r\n    }]\r\n  }, {\r\n    value: 'ningbo',\r\n    label: 'Ningbo',\r\n    isLeaf: true\r\n  }]\r\n}, {\r\n  value: 'jiangsu',\r\n  label: 'Jiangsu',\r\n  children: [{\r\n    value: 'nanjing',\r\n    label: 'Nanjing',\r\n    children: [{\r\n      value: 'zhonghuamen',\r\n      label: 'Zhong Hua Men',\r\n      isLeaf: true\r\n    }]\r\n  }]\r\n}];\r\n\r\n@Component({\r\n  selector: 'nz-demo-cascader-trigger',\r\n  template: `\r\n    {{text}}\r\n    <nz-cascader\r\n      [nzShowInput]=\"false\"\r\n      [nzOptions]=\"nzOptions\"\r\n      [(ngModel)]=\"values\"\r\n      (ngModelChange)=\"onChanges($event)\"\r\n      (nzSelectionChange)=\"onSelectionChange($event)\"\r\n      >\r\n      <a href=\"javascript: void(0)\">Change city</a>\r\n    </nz-cascader>`,\r\n  styles  : [\r\n    `\r\n    .ant-cascader-picker {\r\n      width: 300px;\r\n    }\r\n    `\r\n  ]\r\n})\r\nexport class NzDemoCascaderTriggerComponent {\r\n  /** init data */\r\n  nzOptions = options;\r\n\r\n  /** ngModel value */\r\n  public values: any[] = null;\r\n\r\n  public text = 'Unselect';\r\n\r\n  public onChanges(values: any): void {\r\n    console.log(values, this.values);\r\n  }\r\n\r\n  public onSelectionChange(selectedOptions: any[]): void {\r\n    this.text = selectedOptions.map(o => o.label).join(', ');\r\n  }\r\n}\r\n"