module.exports = "// tslint:disable:no-any\r\nimport { Component } from '@angular/core';\r\n\r\nconst options = [{\r\n  value: 'zhejiang',\r\n  label: 'Zhejiang',\r\n  children: [{\r\n    value: 'hangzhou',\r\n    label: 'Hangzhou',\r\n    children: [{\r\n      value: 'xihu',\r\n      label: 'West Lake',\r\n      code: 752100,\r\n      isLeaf: true\r\n    }]\r\n  }, {\r\n    value: 'ningbo',\r\n    label: 'Ningbo',\r\n    code: '315000',\r\n    isLeaf: true\r\n  }]\r\n}, {\r\n  value: 'jiangsu',\r\n  label: 'Jiangsu',\r\n  children: [{\r\n    value: 'nanjing',\r\n    label: 'Nanjing',\r\n    children: [{\r\n      value: 'zhonghuamen',\r\n      label: 'Zhong Hua Men',\r\n      code: 453400,\r\n      isLeaf: true\r\n    }]\r\n  }]\r\n}];\r\n\r\n@Component({\r\n  selector: 'nz-demo-cascader-custom-render',\r\n  template: `\r\n    <nz-cascader\r\n      [nzLabelRender]=\"renderTpl\"\r\n      [nzOptions]=\"nzOptions\"\r\n      [(ngModel)]=\"values\"\r\n      (ngModelChange)=\"onChanges($event)\">\r\n    </nz-cascader>\r\n\r\n    <ng-template #renderTpl let-labels=\"labels\" let-selectedOptions=\"selectedOptions\">\r\n      <ng-container *ngFor=\"let label of labels; let i = index; let isLast = last\">\r\n        <span *ngIf=\"!isLast\">{{label}} / </span>\r\n        <span *ngIf=\"isLast\">\r\n          {{label}} (<a href=\"javascript:;\" (click)=\"handleAreaClick($event, label, selectedOptions[i])\"> {{selectedOptions[i].code}} </a>)\r\n        </span>\r\n      </ng-container>\r\n    </ng-template>`,\r\n  styles  : [\r\n    `\r\n    .ant-cascader-picker {\r\n      width: 300px;\r\n    }\r\n    `\r\n  ]\r\n})\r\nexport class NzDemoCascaderCustomRenderComponent {\r\n  /** init data */\r\n  nzOptions = options;\r\n\r\n  /** ngModel value */\r\n  public values: any[] = null;\r\n\r\n  public onChanges(values: any): void {\r\n    console.log(values, this.values);\r\n  }\r\n\r\n  handleAreaClick(e: Event, label: string, option: any): void {\r\n    e.preventDefault();\r\n    e.stopPropagation();\r\n    console.log('clicked \\\"', label, '\\\"', option);\r\n  }\r\n}\r\n"